import * as React from "react"
import { Link } from "gatsby"
import { StaticImage } from "gatsby-plugin-image"

import Layout from "../components/layout"
import Seo from "../components/seo"
import { getTokenBalances } from "../utils/TokenBalances.js" 

import { Box, TextInput, Button } from 'grommet'
import { Add } from 'grommet-icons'
import { ethers, utils } from "ethers"
import 'isomorphic-fetch'

const IndexPage = () => {
  const [enteredAddress, setEnteredAddress] = React.useState('')
  const [addresses, setAddresses] = React.useState({})
  const [interval, setIntervalId] = React.useState(null)
  const [lastBlock, setLastBlock] = React.useState(null)
  const [gasPrice, setGasPrice] = React.useState(0)

  const providerOpts = {projectId: process.env.INFURA_PROJECT_ID, projectSecret: process.env.INFURA_PROJECT_SECRET} 
  const [provider, setProvider] = React.useState(null)

  const addAddress = (newAddress) => {
    // TODO: use deep merge here
    let copyAddresses = { ...addresses }
    copyAddresses[newAddress] = { addr: newAddress, wallet: {} }
    setAddresses(copyAddresses)
  }

  const setAndClearAddress = () => {
    addAddress(enteredAddress) 
    setEnteredAddress('')
  }

  const clearAddresses = () => {
    setAddresses([])
    setEnteredAddress('')
  }

  React.useEffect(async () => {
    // TODO: use deep merge here
    let copyAddresses = { ...addresses }
    if (provider && Object.keys(copyAddresses).length) {
      for (let addr of Object.keys(copyAddresses)) {
        copyAddresses[addr].wallet = await getTokenBalances(addr, lastBlock, provider)
      }
      console.log(copyAddresses)
      setAddresses(copyAddresses)
    }
  }, [Object.keys(addresses).length]) 

  React.useEffect(() => {
    if (typeof window !== `undefined`) {
      if (!provider) {
        setProvider(ethers.providers.InfuraProvider.getWebSocketProvider(process.env.ETHERS_NETWORK, process.env.INFURA_PROJECT_ID))
      }
    }
  }, [])

  React.useEffect(() => {
    if (typeof window !== `undefined` && provider) {
      provider.getBlockNumber().then(resp => setLastBlock(resp))
      provider.getGasPrice().then(resp => setGasPrice(resp))
      setIntervalId(setInterval(() => {
        provider.getBlockNumber().then(resp => setLastBlock(resp))
        provider.getGasPrice().then(resp => setGasPrice(resp))
      }, 30000))
      return clearInterval(interval) 
    }
  }, [provider])

  if (!provider) return (<></>)

  return (
    <Layout>
      <Seo title="Home" />
      <Box>
        <Box direction="row" size='xxsmall' align='right' gap='small'>
          <Box size='xxsmall'>block: { lastBlock }</Box>
          <Box size='xxsmall'>gwei: { utils.formatUnits(gasPrice, "gwei") }</Box>
        </Box>
        <Box direction="row" gap='small'>
          <TextInput
            placeholder="enter address..."
            value={enteredAddress}
            onChange={event => setEnteredAddress(event.target.value)}
          />
          <Button 
            primary
            icon={<Add size='small' />}
            color='brand'
            size='xxsmall' 
            onClick={(e) => setAndClearAddress()} 
          />
        </Box>
        {
          Object.entries(addresses).map(([key, val]) => <div>{val.addr}</div>)
        }
      </Box>      
    </Layout>
  )
}

export default IndexPage
